let headVideoBtn = document.querySelector('.head__video-button');
let headVideo = document.querySelector(".head__video");

headVideoBtn && headVideoBtn.addEventListener('click', playPause);

function playPause() {
    if (headVideo.paused) {
        headVideoBtn.classList.add('head__video-button_active');
        headVideo.play();
    } else {
        headVideoBtn.classList.remove('head__video-button_active');
        headVideo.pause();
    }
}